<template>
  <div class="user-manage">
    <div class="btn-group">
      <button type="button" @click="editPage()" class="btn btn-info btn-sm"><i class="fa fa-plus"></i> 新建用户</button>
      <button type="button" class="btn btn-info btn-sm"><i class="fa fa-remove"></i> 删除</button>
      <button type="button" @click="refreshGrid" class="btn btn-info btn-sm"><i class="fa fa-refresh"></i> 刷新</button>
    </div>
    <my-panel :show="show">
      <template slot="first">
        <my-grid @modify="editPage" :refresh="refresh" class="user-list" :config="config"></my-grid>
      </template>
      <template slot="other">
        <edit-page @refresh="gridPage"></edit-page>
      </template>
    </my-panel>
  </div>
</template>

<script>
import EditPage from './EditUser'

export default {
  name: 'user-manage',
  components: {
    EditPage
  },
  data () {
    return {
      config: {
        url: '/user/findAll',
        columns: [
          {
            dataIndex: 'userName',
            text: '姓名',
            renderer (data, record) {
              console.log(data, record)
              return '<i onclick="edit" class="fa fa-edit"> </i> ' + data + ''
            }
          }, {
            dataIndex: 'userAge',
            text: '年龄'
          }, {
            dataIndex: 'userPhone',
            text: '手机号码'
          }
        ]
      },
      show: true,
      refresh: {
        enable: false
      }
    }
  },
  methods: {
    editPage (data) {
      console.log(data)
      const that = this
      that.show = false
    },
    gridPage (back) {
      const that = this
      that.show = true
      if (back && back.refresh) {
        that.refreshGrid()
      }
    },
    refreshGrid () {
      this.refresh = {enable: true}
      setTimeout(() => {
        if (this.refresh.enable) {
          this.refresh = {enable: false}
        }
      }, 3000)
    }
  }
}
</script>
<style>
  .edit-page {
    margin:15px;
  }
  .user-list{
    margin-top:10px;
  }
</style>



