<template>
  <div class="menu">
    <h3>Menu Page</h3>
    <ul>
      <li v-for="item in menuList" v-bind:key="item.url">
        <router-link :to="item.url">{{ item.title }}</router-link>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'my-menu',
  data () {
    return {
      menuList: [{
        title: 'User Manage',
        url: '/userManage'
      }]
    }
  }
}
</script>
<style>
  .menu {
    text-align: center;
  }
</style>



