import Vue from 'vue'
import Router from 'vue-router'
import UserManage from '../components/app/user/UserManage'
import Index from '../components/Hello'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/userManage',
      name: 'userManage',
      component: UserManage
    }, {
      path: '/',
      name: 'index',
      component: Index
    }
  ]
})



