<template>
  <div>
    <table class="table table-bordered table-striped my-table" :style="{display: showTable}">
      <thead>
      <tr>
        <th style="padding-left: 10px;" width="33px"><input v-model="allCheck" type="checkbox" value="1" /></th>
        <th v-for="thcol in config.columns" :key="thcol.dataIndex" :data-column="thcol">{{thcol.text}}</th>
      </tr>
      </thead>
      <tbody>
      <tr :index="index" v-for="(tr, index) in dataList" v-bind:key="tr.userId">
        <td style="padding-left:10px;" width="33px"><input v-model="checks" name="check-ids" :value="index" type="checkbox" /></td>
        <td v-for="col in config.columns" :key="col.dataIndex" :data-column="col" v-html="renderer(tr, col)"></td>
      </tr>
      </tbody>
    </table>
    <div :class="loading">
      <i class="fa fa-circle-o-notch fa-spin" style="font-size:34px; margin: 10px 20px;"></i>
    </div>
  </div>
</template>

<script>
/* eslint-disable vue/no-async-in-computed-properties,no-unused-vars */
import Request from '../../Request.js'

export default {
  props: ['config', 'refresh'],
  data () {
    return {
      dataList: [],
      loading: 'loading',
      showTable: 'none',
      checks: [],
      allCheck: []
    }
  },
  watch: {
    refresh (val) {
      if (val.enable) {
        this.loading = 'loading'
        this.showTable = 'none'
        this.loadGrid(() => {
          val.enable = false
        })
      } else {
        this.loading = 'hidden'
        this.showTable = 'table'
      }
    },
    allCheck (val) {
      let checked = val.length === 0
      if (!checked) {
        for (let i = 0; i < this.dataList.length; i++) {
          this.checks.push(i)
        }
      } else {
        this.checks = []
      }
    }
  },
  methods: {
    loadGrid (callback) {
      const that = this
      Request.get(that.config.url).then((res) => {
        let result = res.data
        setTimeout(() => {
          that.loading = 'hidden'
          that.showTable = 'table'
          if (result.success) {
            that.dataList = result.data
            if (callback) {
              callback()
            }
          }
        }, 200)
      })
    },
    renderer (tr, column) {
      let returnVal = tr[column.dataIndex]
      if (column.renderer) {
        returnVal = column.renderer(tr[column.dataIndex], tr, column)
      }
      return returnVal
    },
    edit (data) {
      this.$emit('modify', data)
    }
  },
  mounted: function () {
    this.$nextTick(function () {
      this.loadGrid()
    })
  }
}
</script>
<style>
  .my-table thead tr th {
    text-align: left;
    padding-left: 20px;
  }

  .my-table tbody tr td {
    text-align: left;
    padding-left: 20px;
  }
</style>



