<template>
  <div class="edit-page">
    <div class="btn-group">
      <button type="button" @click="saveUser" class="btn btn-info btn-sm"><i class="fa fa-check"></i> 保存</button>
      <button type="button" @click="backList" class="btn btn-info btn-sm"><i class="fa fa-arrow-left"></i> 返回</button>
    </div>
    <div class="edit-form form-group">
      <label>Name</label>
      <input class="form-control" v-model="name" />
    </div>
  </div>
</template>

<script>
export default {
  name: 'edit-page',
  data () {
    return {
      name: 'test'
    }
  },
  methods: {
    backList (e) {
      this.$emit('refresh', {refresh: true})
    },
    saveUser (e) {
      console.log('save')
    }
  }
}
</script>
<style>
  .edit-page {
    margin:15px;
  }
  .edit-form{
    margin-top:10px;
  }
</style>



