<template>
  <div>
    <div :class="{ hidden: !show }">
      <slot name="first"></slot>
    </div>
    <div :class="{ hidden: show }">
      <slot name="other"></slot>
    </div>
  </div>
</template>

<script>
/* eslint-disable vue/no-async-in-computed-properties,no-unused-vars */
import Request from '../../Request.js'

export default {
  props: ['show'],
  data () {
    return {
      showNow: true
    }
  }
}
</script>
<style>
  .my-table thead tr th{
    text-align: center;
  }
  .my-table tbody tr td{
    text-align: center;
  }
</style>



